
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'accoundBind',
directives: {
TransferDom
},
components: {
XInput,
Group,
XButton,
Confirm
},
data () {
return {
bindAccountList: [],
isShow: false,
operItem: {
name: ''
}
}
},
created () {
this.getAccount()
},
methods: {
getWechat () {
let that = this
window.wechat = function (openid, name) {
that.$vux.loading.show({
text: '加载中...'
})
let params = {
account: name,
openid: openid,
accountType: that.operItem.accountType,
merchantId: that.$store.state.merchantId,
operate: 0
}
that.$http.fetchPost('/merchant/center/update/acct', params).then((res) => {
setTimeout(() => {
that.$vux.loading.hide()
}, 500)
if (res.data.code === 200) {
that.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
that.isShow = false
that.getAccount()
} else {
that.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
let ua = navigator.userAgent.toLowerCase();

let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;

let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {

window.webkit.messageHandlers.onOpenWxListener.postMessage(null)
} else if (/(Android)/i.test(navigator.userAgent)) {

window.AndroidListener.onOpenWxListener()
}
},
getBack () {
this.$router.go(-1)
},
getAccount () {
this.$http.fetchGet('/merchant/center/get/acctlist', {merchantId: this.$store.state.merchantId}).then((res) => {
this.bindAccountList = res.data.data.bindAccountList

this.bindAccountList.forEach(item => {
item['name'] = this.dealAccountType(item.accountType)
})
})
},

dealAccountType (type) {
let name = ''
switch (type) {
case 0: name = '微信'; break
case 1: name = '支付宝'; break
case 2: name = '银行卡'; break
default: name = ''; break
}
return name
},

bind (item) {
this.operItem = item
if(item.account){

this.offBind(item)
}else{

if(item.accountType == 0) {

this.getWechat()
}else{
this.isShow = true
}
}
},

onConfirm (msg) {
if (msg) {
this.bindPost(msg)
} else {
this.$vux.toast.show({
text: '请输入账号',
position: 'middle',
type: 'warn'
})
}
},
offBind (item) {
this.$vux.loading.show({
text: '加载中...'
})
let params = {
account: '',
accountType: this.operItem.accountType,
openid: item.openid || '',
merchantId: this.$store.state.merchantId,
operate: 1
}
this.$http.fetchPost('/merchant/center/update/acct', params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
this.isShow = false
this.getAccount()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
bindPost (msg) {
let params = {
account: msg,
accountType: this.operItem.accountType,
merchantId: this.$store.state.merchantId,
operate: 0
}
this.$http.fetchPost('/merchant/center/update/acct', params).then((res) => {
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
this.isShow = false
this.getAccount()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},

onCancel () {
this.isShow = false
}
}
}



